  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10vh;
  max-width: 800px;
}
.page-home p {
  font-weight: var(--fw-light);
}
.page-home button {
  font-size: var(--fs-500);
  margin-top: 5vh;
  padding: 0.5rem 3rem;
  border-radius: 1.5rem;
  background-color: hsl(var(--clr-accent-500));
  border: 4px solid hsl(var(--clr-accent-500));
  transition: background-color 0.4s ease;
}
.page-home button:hover {
  background-color: hsl(var(--bg-primary));
}
.pizza-img,
.basil-img,
.basil-right-img {
  display: none;
  position: absolute;
  width: auto;
  aspect-ratio: 16 / 9;
  z-index: -1;
}
.pizza-img {
  bottom: 0;
  right: 0;
  height: 20%;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat right;
  background-size: contain;
}
.basil-img {
  bottom: 20%;
  left: 10%;
  height: 10%;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat left;
  background-size: contain;
}
.basil-right-img {
  bottom: 40%;
  right: 20%;
  height: 5%;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat right;
  background-size: contain;
  transform: rotate(90deg);
}

@media screen and (min-height: 600px) and (min-width: 300px) {
  .pizza-img,
  .basil-img {
    display: block;
  }
}
@media screen and (min-height: 600px) and (min-width: 750px) {
