___CSS_LOADER_EXPORT___.push([module.id, `.page-contact {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-areas: 
    'content'
    'image';
  width: 100%;
  border: 1px solid hsla(var(--clr-primary-500), 0.1);
}
.contact-content {
  grid-area: content;
  display: flex;
  flex-direction: column;
}
.contact-content h1 {
  display: inline-flex;
  flex-direction: column;
  align-items: end;
  margin-left: auto;
  padding: 1rem 2rem;
  line-height: 2rem;
  text-align: end;
  background-color: hsla(var(--clr-primary-500), 0.1);
  border-bottom-left-radius: 3rem;
}
.contact-content .subheader {
  font-size: var(--fs-450);
}
.contact-content ul {
  flex-grow: 1;
  display: grid;
  align-content: center;
  row-gap: 0.5rem;
  list-style: none;
  padding: 2rem 0 2rem 0.5rem;
}
.contact-content li {
  display: grid;
  column-gap: 0.5rem;
}
.contact-content li .icon {
  display: none;
  grid-column: 1;
  grid-row: 1 / span 2;
  width: 32px;
  height: auto;
  fill: hsl(var(--color-primary-500));
}
.contact-content h2 {
  font-size: var(--fs-450);
  line-height: 1.5rem;
}
.contact-content li p {
  font-weight: var(--fw-light);
}
.contact-img {
  grid-area: image;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center;
  background-size: cover;
}

@media screen and (min-height: 650px) {
  .contact-img {flex-grow: 1;}
}
@media screen and (min-width: 450px) {
  .contact-content li {
    grid-template-columns: auto 1fr;
  }
  .contact-content li .icon {
    display: block;
  }
}
@media screen and (min-width: 800px) {
  .page-contact {
    max-height: 600px;
    grid-template-rows: auto;
    grid-template-columns: 35% 1fr;
    grid-template-areas: 'image content';
  }
  .contact-content {
    align-items: center;
  }
}
@media screen and (min-width: 1400px) {
  .contact-content ul {
    grid-template-columns: 1fr 1fr;
    row-gap: 3rem;
  }
