  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff2');
  font-weight: 200;
  font-style: normal;
}
:root {
  --clr-primary-500: 0deg, 0%, 12%;
  --clr-secondary-500: 359deg, 86%, 38%;
  --clr-accent-500: 133deg, 56%, 47%;
  --bg-primary: 0deg, 0%, 85%;

  --ff-primary: 'Poppins', sans-serif;

  --fw-light: 200;
  --fw-regular: 400;
  --fs-400: 1rem;
  --fs-450: 1.25rem;
  --fs-500: 1.5rem;
  --fs-600: 2rem;
  --fs-700: 2.5rem;
  --fs-800: 3rem;

}
/* ====================|| CSS RESET ||==================== */
/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
input, button, textarea, select {
  font: inherit;
}
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
  font-weight: var(--fw-regular);
}
#root, #__next {
  isolation: isolate;
}
body {
  font-size: var(--fs-400);
  font-family: var(--ff-primary);
  color: hsl(var(--clr-primary-500));
  background-color: hsl(var(--bg-primary));
  overflow-y: scroll;
}
.container {
  padding: 0.5rem;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
h1 {
  line-height: 2.5rem;
  font-size: var(--fs-700);
}
h2 {
  line-height: 2rem;
  font-size: var(--fs-600);
}
h3 {
  line-height: 1.5rem;
  font-size: var(--fs-450);
}
#app {
  display: grid;
  grid-template-rows: auto 1fr;
  min-height: 100dvh;
}
button {
  color: hsl(var(--clr-primary-500));
  cursor: pointer;
  border: none;
}

@media screen and (min-width: 500px) {
  .container {
    padding: 1rem;
  }
}
@media screen and (min-width: 900px) {
  .container {
    padding: 2rem;
  }
}
@media screen and (min-width: 1100px) {
  #app {
    position: relative;
    width: min(100%, 1500px);
    justify-self: center;
  }
